// =========================================================================================
  
    .header__container {
  
      padding-top: 20px;
  
      @media screen and (min-width: 768px) {
        padding-top: 32px;
  
      }
    }
  
    .header__background {
      padding: 14px 14px 20px 14px;
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      max-width: 280px;
      border-radius: 10px;
      min-height: 460px;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      text-decoration: none;
  
      background-image: linear-gradient(to right,
          rgba(0, 0, 0, 0.4),
          rgba(0, 0, 0, 0.4)),
        url(../img/background-mob.jpg);
  
      @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
        background-image: linear-gradient(to right,
            rgba(0, 0, 0, 0.4),
            rgba(0, 0, 0, 0.4)),
          url(../img/background-mob\ @2x.jpg);
      }
  
      @media (min-device-pixel-ratio: 3),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
        background-image: linear-gradient(to right,
            rgba(0, 0, 0, 0.4),
            rgba(0, 0, 0, 0.4)),
          url(../img/background-mob@3x.jpg);
      }
  
      @media screen and (min-width: 768px) {
        min-width: 704px;
        min-height: 222px;
        padding: 16px;
        border-radius: 15px;
  
        background-image: linear-gradient(to right,
            rgba(0, 0, 0, 0.4),
            rgba(0, 0, 0, 0.4)),
          url(../img/background-tab1x.jpg);
  
        @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
          background-image: linear-gradient(to right,
              rgba(0, 0, 0, 0.4),
              rgba(0, 0, 0, 0.4)),
            url(../img/background-tab2x.jpg);
        }
  
        @media (min-device-pixel-ratio: 3),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
          background-image: linear-gradient(to right,
              rgba(0, 0, 0, 0.4),
              rgba(0, 0, 0, 0.4)),
            url(../img/background-tab3x.jpg);
        }
      }
  
  
      @media screen and (min-width: 1280px) {
        min-width: 1216px;
        min-height: 316px;
        padding: 16px 32px 32px 32px;
        border-radius: 15px;
        background-image: linear-gradient(to right,
            rgba(0, 0, 0, 0.49),
            rgba(0, 0, 0, 0.49)),
          url(../img/images-background.jpg);
  
        @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
          background-image: linear-gradient(to right,
              rgba(0, 0, 0, 0.49),
              rgba(0, 0, 0, 0.49)),
            url(../img/background-desk@2x.jpg);
        }
  
        @media (min-device-pixel-ratio: 3),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
          background-image: linear-gradient(to right,
              rgba(0, 0, 0, 0.49),
              rgba(0, 0, 0, 0.49)),
            url(../img/background-desk@3x.jpg);
        }
  
      }
    }
  
    .header__wrapper {
      display: flex;
      flex-direction: column;
      gap: 4px;
  
      @media screen and (min-width: 768px) {
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        justify-content: flex-end;
  
        @media screen and (min-width: 1280px) {
          padding-top: 0;
          gap: 8px;
        }
      }
    }
  
    .nav__list {
      display: flex;
      list-style: none;
  
      @media screen and (min-width: 768px) {
  
        margin-bottom: 0px;
        margin-top: 0;
        padding-top: 0;
        padding-left: 0;
      }
    }

            .button {
            align-items: center;
              border: 0;
              border-color: inherit;
            border-radius: 100px;
              font-weight: 500;
                font-size: 12px;
                line-height: calc(14 / 12);
                text-transform: uppercase;
                cursor: pointer;
                color: #000000;
                  background: #ffffff;
            }
  
    .nav__item-home {
      padding: 10px 26px;
      width: 87px;
      height: 34px;
  
  
      @media screen and (min-width: 768px) {
        width: 83px;
        height: 38px;
        padding: 12px 24px;
      }
  
      @media screen and (min-width: 1280px) {
        margin-left: 8px;
        width: 99px;
        height: 42px;
        padding: 14px 32px;
      }
    }

    .current {
      background-color: #000000;
      color:#ffffff;
    }
  
    .nav__item-library {
  
      width: 113px;
      height: 34px;
      margin-left: 4px;
  
      @media screen and (min-width: 768px) {
        width: 109px;
        height: 38px;
        padding: 12px 20px;
      }
  
      @media screen and (min-width: 1280px) {
        margin-left: 8px;
        width: 117px;
        height: 42px;
        padding: 14px 24px;
      }
    }
  
    .header__form {
      display: flex;
      max-width: 252px;
  
      @media screen and (min-width: 1280px) {
  
        height: 42px;
      }
    }
  
    .header__form-input {
      height: 34px;
      padding-left: 14px;
      padding-right: 0;
      border: 0;
      outline: transparent;
      border-top-left-radius: 100px;
      border-bottom-left-radius: 100px;
  
      @media screen and (min-width: 768px) {
        max-width: 120px;
        height: 38px;
      }
  
      color: #000000;
  
      @media screen and (min-width: 1280px) {
        max-width: 142px;
        height: 42px;
      }
    }
  
    .header__form-btn {
      width: 45px;
      padding: 7px;
      cursor: pointer;
      background-color: #ffffff;
      border: none;
  
      justify-content: center;
      align-items: center;
      display: flex;
      border-top-right-radius: 100px;
      border-bottom-right-radius: 100px;
    }
  
    .header__form-input::placeholder {
      font-weight: 400;
      font-size: 12px;
      line-height: calc(14 / 12);
  
      color: #000000;
  
      @media screen and (min-width: 768px) {
        font-size: 14px;
        line-height: calc(16 / 14);
      }
    }
  
    .header__form-btn {
      border-left: 0;
      border-color: inherit;
    }
  
    .header__form-svg {
      display: inline-block;
      width: 14px;
      height: 14px;
      stroke-width: 0;
      stroke: currentColor;
      stroke: #000000;
      fill: #ffffff;
  
      @media screen and (min-width: 768px) {
        width: 16px;
        height: 16px;
      }
    }
  
