    .header__logo-link {
      display: flex;
      align-items: center;
      text-decoration: none;
    }
  
    .header__logo-svg {
      display: inline-block;
      width: 32px;
      height: 32px;
      margin-right: 8px;
      stroke-width: 0;
      stroke: #ffffff;
      fill: transparent;
  
      @media screen and (min-width: 768px) {
        width: 40px;
        height: 40px;
      }
  
      @media screen and (min-width: 1280px) {
        width: 59px;
        height: 59px;
        margin-right: 16px;
      }
    }
  
    .header__title {
      text-decoration: none;
      font-weight: 500;
      font-size: 32px;
      line-height: 38px;
      line-height: calc(32 / 38);
      color: #ffffff;
  
      @media screen and (min-width: 768px) {
        font-size: 40px;
        line-height: calc(47 / 40);
      }
  
      @media screen and (min-width: 1280px) {
        font-size: 64px;
        line-height: calc(75 / 64);
      }
    }